import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Doughnut, Pie } from 'react-chartjs-2'
import { Chart as ChartJS, ArcElement, Tooltip, Legend, Title } from 'chart.js'
import { loadToys } from '../store/actions/toyAction'
import { chartService } from '../services/chart.service'

ChartJS.register(ArcElement, Tooltip, Legend, Title)

const inventoryByTypeOptions = {
  plugins: {
    title: {
      display: true,
      text: 'Toy inventory by type',
    },
  },
}

const avgPriceByTypeOptions = {
  plugins: {
    title: {
      display: true,
      text: 'Average price by type',
    },
  },
}

export const Dashboard = () => {
  const { toys } = useSelector((state) => state.toyReducer)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(loadToys())
  }, [])

  const toysByType = chartService.getToyCountByType(toys)
  const toysByTypeData = {
    labels: toysByType.labels,
    datasets: [
      {
        data: toysByType.data,
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
        ],
        borderWidth: 1,
      },
    ],
  }

  const avgPriceByType = chartService.getAvgPriceByType(toys)
  const avgPriceByTypeData = {
    labels: avgPriceByType.labels,
    datasets: [
      {
        data: avgPriceByType.data,
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
        ],
        borderWidth: 1,
      },
    ],
  }

  if (!toys?.length) return <div>Loading...</div>
  return (
    <section className='charts-container'>
      <div className='chart-container'>
        <Doughnut data={toysByTypeData} options={inventoryByTypeOptions} />
      </div>
      <div className='chart-container'>
        <Pie data={avgPriceByTypeData} options={avgPriceByTypeOptions} />
      </div>
    </section>
  )
}
