const getToyCountByType = (toys) => {
  const toyCountByType = toys.reduce((map, toy) => {
    if (toy.type) {
      if (!map[toy.type]) map[toy.type] = 0
      if (toy.inStock) map[toy.type]++
    }
    return map
  }, {})
  return {
    labels: Object.keys(toyCountByType),
    data: Object.values(toyCountByType),
  }
}

const getAvgPriceByType = (toys) => {
  const avgPriceByType = toys.reduce((map, toy) => {
    if (toy.type) {
      if (!map[toy.type]) map[toy.type] = { count: 0, price: 0 }
      map[toy.type].count++
      map[toy.type].price += toy.price
    }
    return map
  }, {})
  const data = Object.keys(avgPriceByType).map(
    (label) => avgPriceByType[label].price / avgPriceByType[label].count
  )
  return { labels: Object.keys(avgPriceByType), data: data }
}

export const chartService = {
  getToyCountByType,
  getAvgPriceByType,
}
