import React from 'react'
import { NavLink } from 'react-router-dom'
import Button from '@material-ui/core/Fab';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles } from '@material-ui/core/styles';
import { red } from '@material-ui/core/colors';
import Card from '@material-ui/core/Card';
import { CardHeader, CardMedia, Typography, CardContent } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
}));



export function ToyPreview(props) {
    const classes = useStyles();
    const { toy, onRemove } = props
    return (
        <Card className={`${classes.root} toy-card`}>
            <CardHeader title={toy.name} subheader={toy.type} action={<IconButton color="inherit" onClick={() => { onRemove(toy._id) }} aria-label="delete" className={`${classes.margin} delete-card`}>
                <DeleteIcon fontSize="large" />
            </IconButton>} />

            <CardMedia
                image={`https://robohash.org/${toy.name}?set=set2`}
                title="TOYY"
                className={classes.media}
            />

            <CardContent>
                <Typography variant="body1" color="textSecondary" component="p">
                    In Stock:  {(toy.inStock) ? ' true' : ' false'}
                </Typography>
                <Typography variant="body1" color="textSecondary" component="p">
                    Price:   {`$${toy.price}`}
                </Typography>
            </CardContent>

            <section className="toy-prev-btns">
                <Button className="btn-link" href="#text-buttons" color="primary">

                    <NavLink to={`/toy/edit/${toy._id}`}>Edit</NavLink>
                </Button>

                <Button className="btn-link" href="#text-buttons" color="primary">
                    <NavLink to={`/toy/details/${toy._id}`}>Details</NavLink>
                </Button>
            </section>
        </Card>
    )
}
