import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { Formik, Form, Field } from 'formik'
import * as Yup from 'yup'
import { toyService } from '../services/toy.service'
import Button from '@material-ui/core/Button'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import TextField from '@material-ui/core/TextField'
import { saveToy } from '../store/actions/toyAction'

export function ToyEdit() {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const { toyId } = useParams()
  const [toyToEdit, setToyToEdit] = useState({
    name: '',
    price: '',
    type: '',
    inStock: '',
  })

  useEffect(() => {
    if (!toyId) return
    toyService.getById(toyId).then((toy) => {
      setToyToEdit(toy)
    })
  }, [])

  const ToySchema = Yup.object().shape({
    name: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Required'),
    price: Yup.number().min(0, 'Too Short!').required('Required'),
    inStock: Yup.boolean(),
  })

  const onSave = (values) => {
    dispatch(saveToy(values)).then(() => {
      navigate('/toy')
    })
  }

  const CustomInputComponent = (props) => (
    <TextField
      variant='outlined'
      type='text'
      className='edit-input name-input'
      id='outlined-basic'
      autoComplete='false'
      {...props}
    />
  )

  if (!toyToEdit) return <div>Loading...</div>
  return (
    <Formik
      initialValues={toyToEdit}
      validationSchema={ToySchema}
      onSubmit={onSave}
      enableReinitialize
    >
      {({ errors, touched, values }) => {
        return (
          <Form className='container edit-form'>
            <Field
              name='name'
              id='outlined-basic'
              as={CustomInputComponent}
              label='Name'
            />
            {errors.name && touched.name ? <div>{errors.name}</div> : null}
            <Field
              name='price'
              id='outlined-basic'
              as={CustomInputComponent}
              label='Price'
            />
            {errors.price && touched.price ? <div>{errors.price}</div> : null}
            <Field as={Select} name='type' value={values.type || 'Type'}>
              <MenuItem value='Type' selected>
                Type
              </MenuItem>
              <MenuItem value='Funny'>Funny</MenuItem>
              <MenuItem value='Adult'>Adult</MenuItem>
              <MenuItem value='Educational'>Educational</MenuItem>
            </Field>
            <div>
              <Field
                as={Select}
                name='inStock'
                id='inStock'
                value={
                  typeof values.inStock === 'boolean'
                    ? values.inStock
                    : 'In Stock'
                }
              >
                <MenuItem value={'In Stock'} disabled>
                  In Stock
                </MenuItem>
                <MenuItem value={true}>Yes</MenuItem>
                <MenuItem value={false}>No</MenuItem>
              </Field>
            </div>
            <Button variant='contained' type='submit'>
              Submit
            </Button>
          </Form>
        )
      }}
    </Formik>
  )
}
