import React from 'react'
import GoogleMapReact from 'google-map-react'
import { useState } from 'react'
import { Button } from '@material-ui/core'

const branches = [
  {
    city: 'Haifa',
    pos: {
      lat: 32.794,
      lng: 34.9896,
    },
  },
  {
    city: 'Tel-Aviv',
    pos: {
      lat: 32.0853,
      lng: 34.7818,
    },
  },
  {
    city: 'Eilat',
    pos: {
      lat: 29.5577,
      lng: 34.9519,
    },
  },
]

const Marker = () => (
  <div
    style={{ height: 12, width: 12, borderRadius: '50%', background: 'red' }}
  ></div>
)

export const About = () => {
  const zoom = 10
  const [center, setCenter] = useState({
    lat: 32.0853,
    lng: 34.7818,
  })

  const onClick = ({ x, y, lat, lng, event }) => {
    setCenter({ lat, lng })
  }

  return (
    <div style={{ height: '100vh', width: '100%' }}>
      <GoogleMapReact
        onClick={onClick}
        bootstrapURLKeys={{ key: 'AIzaSyBZ9rdVSQnQ_VgaChu60lkDetLhzHoqCCg' }}
        defaultCenter={center}
        center={center}
        defaultZoom={zoom}
      >
        <Marker lat={center.lat} lng={center.lng} />
      </GoogleMapReact>

      <div className='branches'>
        {branches.map((branch) => (
          <Button key={branch.city} onClick={() => setCenter(branch.pos)}>
            {branch.city}
          </Button>
        ))}
      </div>
    </div>
  )
}
