import React, { useState } from 'react'
import { useEffect } from 'react'
import { useParams } from "react-router-dom"
import { toyService } from '../services/toy.service'

export function ToyDetails() {
    const { toyId } = useParams()
    const [toy, setToy] = useState(null)

    useEffect(() => {
        loadToy()
    }, [])

    const loadToy = () => {
        toyService.getById(toyId).then(setToy)
    }

    if (!toy) return <div>Loading...</div>
    return (
        <div className="toy-details">
            <h1>Name: <span>{toy.name}</span></h1>
            <h1>Price: <span>{toy.price}</span></h1>
            <h1>Type: <span>{toy.type}</span></h1>
            <h1>Created at: <span>{toy.createdAt}</span></h1>
            <h1>In Stock: <span>{(toy.inStock) ? 'yes' : 'no'}</span></h1>
        </div>
    )
}



