import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import { ToyList } from '../cmps/toy-list'
import { ToyFilter } from '../cmps/toy-filter'
import { loadToys, removeToy } from '../store/actions/toyAction'

export function ToyApp() {
  const { toys } = useSelector((state) => state.toyReducer)
  const [filterBy, setFilterBy] = useState({
    search: '',
    maxPrice: Infinity,
    type: 'All',
    inStock: undefined,
  })

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(loadToys(filterBy))
  }, [filterBy])

  const onRemoveToy = (toyId) => {
    dispatch(removeToy(toyId))
  }

  const handleChange = (ev) => {
    const field = ev.target.name
    const value = field === 'inStock' ? ev.target.checked : ev.target.value
    setFilterBy({ ...filterBy, [field]: value })
  }

  if (!toys) return <div>Loading...</div>
  return (
    <div className='toy-app'>
      <section className='main-control-container'>
        <Button className='add-link'>
          <NavLink to='/toy/edit'>Add Toy</NavLink>
        </Button>

        <ToyFilter filterBy={filterBy} handleChange={handleChange} />
      </section>

      <ToyList toys={toys} onRemove={onRemoveToy} />
    </div>
  )
}
